/* --------------------------------------------------------------------------
Single variable font.

Note that you may want to do something like this to make sure you're serving
constant fonts to older browsers:
html {
  font-family: 'Inter UI', sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter UI var', sans-serif;
  }
}

BUGS:
- Safari 12.0 will default to italic instead of regular when font-weight
  is provided in a @font-face declaration.
  Workaround: Use "Inter UI var alt" for Safari, or explicitly set
  `font-variation-settings:"slnt" DEGREE`.
*/
@font-face {
  font-family: "Inter UI var";
  font-weight: 100 900; /* prev range was 400 900 */
  font-display: swap;
  font-style: oblique 0deg 10deg;
  src: url(../../static/media/Inter-UI.var.530180a3.woff2) format("woff2-variations"),
    url(../../static/media/Inter-UI.var.530180a3.woff2) format("woff2");
}

/* --------------------------------------------------------------------------

"Inter UI var alt" is recommended for Safari and Edge, for reliable italics.

@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter UI var alt', sans-serif;
  }
}

*/
@font-face {
  font-family: "Inter UI var alt";
  font-weight: 100 900;
  font-display: swap;
  font-style: normal;
  font-named-instance: "Regular";
  src: url(../../static/media/Inter-UI-upright.var.fc804a83.woff2)
      format("woff2 supports variations(gvar)"),
    url(../../static/media/Inter-UI-upright.var.fc804a83.woff2) format("woff2-variations"),
    url(../../static/media/Inter-UI-upright.var.fc804a83.woff2) format("woff2");
}
@font-face {
  font-family: "Inter UI var alt";
  font-weight: 100 900;
  font-display: swap;
  font-style: italic;
  font-named-instance: "Italic";
  src: url(../../static/media/Inter-UI-italic.var.f7ec46fa.woff2)
      format("woff2 supports variations(gvar)"),
    url(../../static/media/Inter-UI-italic.var.f7ec46fa.woff2) format("woff2-variations"),
    url(../../static/media/Inter-UI-italic.var.f7ec46fa.woff2) format("woff2");
}

